@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
html,body {
  height: 100%;
}

body {
  /* reset body margin and padding */
  font-family: sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.menu {
  position: relative;
  display: flex;
  flex-wrap: nowrap;
  flex-grow: 2;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0;
  text-align: center;
  z-index: 1;
}

.menu > nav {
  box-shadow: none;
}

.nav-wrapper {
  position: relative;
  order: 1;
  flex-grow: 3;
  background: inherit;
  box-shadow: none;
  margin: 0 30px;
}

.brand-logo.right {
  position: relative;
  height: 4rem;
  margin: 0 auto;
}

.navigation__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  /* reset list styles for navigation */
  margin: 0;
  padding: 0;
}

@media only screen and (max-width: 950px) {
  .menu {
    flex-direction: column;
  }
  .navigation__list {
    flex-direction: column;
  }
}

.navigation__item {
  flex-grow: 1;
  list-style-type: none;
}

.navigation__link {
  text-decoration: none;
  text-transform: uppercase;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.navigation__link:hover {
  color: #fe8b68;
}

.welcome {
  display: flex;
  justify-content: flex-end;
  text-align: right;
  overflow: hidden;
  padding: 50px 50px;
}

.welcome__content {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
  margin: .5rem;
}

.welcome__image {
  order: 3;
  flex-shrink: 0;
}

.welcome__slogan {
  text-transform: uppercase;
  font-size: 4rem;
  font-weight: bold;
  margin: 0;
  margin-bottom: 0.3em;
  line-height: 1em;
  letter-spacing: 0.03em;
  -webkit-transform: translateY(500px);
          transform: translateY(500px);
  -webkit-transition-property: -webkit-transform;
  transition-property: -webkit-transform;
  transition-property: transform;
  transition-property: transform, -webkit-transform;
  -webkit-transition-duration: 1s;
          transition-duration: 1s;
  -webkit-transition-delay: 0s;
          transition-delay: 0s;
}

.welcome__slogan span {
  display: inline-block;
  -webkit-transition-property: -webkit-transform;
  transition-property: -webkit-transform;
  transition-property: transform;
  transition-property: transform, -webkit-transform;
  -webkit-transition-duration: 1s;
          transition-duration: 1s;
  -webkit-transition-delay: 0s;
          transition-delay: 0s;
}

.welcome__slogan span:hover {
  -webkit-animation-duration: 0.6s;
          animation-duration: 0.6s;
  -webkit-animation-name: pulse;
          animation-name: pulse;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
}

@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  50% {
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes pulse {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  50% {
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

.welcome__slogan--animated {
  -webkit-transform: translateY(0px);
          transform: translateY(0px);
}

.welcome__button {
  text-transform: uppercase;
  text-decoration: none;
  width: 200px;
  -webkit-transform: translateY(500px);
          transform: translateY(500px);
  -webkit-transition-property: -webkit-transform;
  transition-property: -webkit-transform;
  transition-property: transform;
  transition-property: transform, -webkit-transform;
  -webkit-transition-duration: 1s;
          transition-duration: 1s;
  -webkit-transition-delay: 0.6s;
          transition-delay: 0.6s;
}

.welcome__button:hover {
  box-shadow: 0px 2px 2px 2px rgba(0,0,0,.6);
  border-bottom: 1.5px solid rgba(255,255,255,0.4);
}

.welcome__button--animated {
  -webkit-transform: translateY(0px);
          transform: translateY(0px);
}

@media only screen and (max-width: 820px) {
  .welcome {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .welcome__content {
    flex-direction: column;
    text-align: center;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
  }

  .welcome__slogan {
    font-size: 2rem;
  }

  .welcome__image {
    margin-right: 0;
    margin-bottom: 20px;
    clear: both;
  }
}

.details {
  display: flex;
  flex-wrap: wrap;
  padding: 30px 5%;
  margin: 0 auto;
}

.details__top {
  display: block;
}

.details__section {
  display: block;
  flex: 1 1 49%;
  flex-wrap: wrap;
  margin: 0.2rem;
}

.details__image {
  margin: 0 auto;
  width: 100%;
}

.separator {
  width: 100%;
  border-top: 1px solid #272727;
  clear: both;
}

.details__footer {
  text-align: left;
  width: 100%;
  clear: both;
}

.email {
  color: #41a0f1;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.email:hover {
  color: #fe8b68;
}

.section__head,
.footer__head {
  font-size: 1.4em;
  text-transform: uppercase;
}

.page-footer {
  padding: 20px;
  font-size: 0.8rem;
  text-align: center;
  background-color: #3B3B3B;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

